import ApiSelector from '../../../components/api-selector/index.vue';

export default {
    components: { ApiSelector },
    props: {
        form_cfg: { type: Object, required: false },
        form_databinding: {
            type: Object, required: false, default() {
                return {};
            }
        },
    },
    data() {
        return {
            read: false,
            isShowApiSelector: false,
            apiList: [] as ApiListSelectedReuslt[],
            crudApi: { //  index
                create: -1, read: -1, update: -1, delete: -1
            },
            infoUrl: ' Read API'
        };
    },
    methods: {
        getApi(): string {
            return process.env.NODE_ENV === 'development' ? '/dp_service' : '/dp';
        },

        /**
         *  api crud 
         */
        rendererApiList(list: ApiListSelectedReuslt[]): void {
            this.apiList = list;
        },

        getInfoApi(): void {
            if (this.crudApi.read !== -1)
                this.infoUrl = this.apiList[this.crudApi.read].url;
        },

        doRead(): void {
            alert(9);
        },

        doCreate(): void {
            alert(9);
        },
        doUpdate(): void {
            alert(9);
        },

        del(): void {
            this.$Modal.confirm({
                title: '',
                content: '',
                okText: '',
                cancelText: '',
                onOk: () => {
                    // TODO
                }
            });
        }
    }
};